import React, { Component } from 'react';
import { inject, observer } from "mobx-react";
import lodash from 'lodash';

@inject("userStore")
@observer
export default class Register extends Component {
	state = {
		error: {},
		loading: false,
		organization: '',
		firstname: '',
		lastname: '',
		phonenumber: '',
		email: '',
		password: '',
		password_confirmation: ''
	};

	inputChanged(name, event) {
		let val = event.target.value;
		this.setState((state)=>state[name]=val);
	}

	register = (event) => {
		event.preventDefault();
		this.setState({loading: true});
		let {organization,firstname,lastname,phonenumber,email,password,password_confirmation} = this.state;
		axios.post('/auth/register',{organization,firstname,lastname,phonenumber,email,password,password_confirmation}).then(res=>{
			this.props.userStore.setUser(res.data);
		}, error=> {
			error = error.response ? error.response.data : error;
			this.setState({error});
		}).finally(()=>this.setState({loading: false}));
	}

	render() {
		let error = this.state.error;
		let firstnameInvalid = '', lastnameInvalid='', phonenumberInvalid='', emailInvalid='', organizationInvalid='', passwordInvalid='', confirmPasswordInvalid='';
		if(error && error.errors) {
			firstnameInvalid = error.errors.firstname ? 'is-invalid' : '';
			lastnameInvalid = error.errors.lastname ? 'is-invalid' : '';
			phonenumberInvalid = error.errors.phonenumber ? 'is-invalid' : '';
			emailInvalid = error.errors.email ? 'is-invalid' : '';
			organizationInvalid = error.errors.organization ? 'is-invalid' : '';
			passwordInvalid = error.errors.password ? 'is-invalid' : '';
			confirmPasswordInvalid = error.errors.password_confirmation ? 'is-invalid' : '';
		}

		return <form id="form-signup" onSubmit={this.register} >
					  <input type="hidden" name="_csrf-frontend" value="bopX9T1X5-hZYeTlpvYMKgPA2UH2-stdJzYJKmGwSvsH72-XBBvRvg0KvofWn3hfTLm2G9ugpAdLeFliTModyw==" />
					  <div className="card card-register card-plain text-center">
					    <div className="card-header ">
					      <div className="row  justify-content-center">
					        <div className="col-md-8">
					          <div className="header-text">
					            <h2 className="card-title">ShulePoint</h2>
					            <h4 className="card-subtitle">Register for free and experience the dashboard today</h4>
					            <hr />
					          </div>
					        </div>
					      </div>
					    </div>
					    <div className="card-body ">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					      <div className="row">
					        <div className="col-md-5 ml-auto">
					          <div className="media">
					            <div className="media-left">
					              <div className="icon">
					                <i className="nc-icon nc-circle-09"></i>
					              </div>
					            </div>
					            <div className="media-body">
					              <h4>Free Account</h4>
					              <p>Here you can write a feature description for your dashboard, let the users know what is the value that you give them.</p>
					            </div>
					          </div>
					          <div className="media">
					            <div className="media-left">
					              <div className="icon">
					                <i className="nc-icon nc-preferences-circle-rotate"></i>
					              </div>
					            </div>
					            <div className="media-body">
					              <h4>Awesome Performances</h4>
					              <p>Here you can write a feature description for your dashboard, let the users know what is the value that you give them.</p>
					            </div>
					          </div>
					          <div className="media">
					            <div className="media-left">
					              <div className="icon">
					                <i className="nc-icon nc-planet"></i>
					              </div>
					            </div>
					            <div className="media-body">
					              <h4>Global Support</h4>
					              <p>Here you can write a feature description for your dashboard, let the users know what is the value that you give them.</p>
					            </div>
					          </div>
					        </div>
					        <div className="col-md-4 mr-auto">
					          <div className="card card-plain">
					            <div className="content">
					              <div className="form-group">
					                <div className="form-group field-signupform-organization required">
					                  <input value={this.state.organization} onChange={this.inputChanged.bind(this,'organization')} type="text" id="signupform-organization" className={`form-control ${organizationInvalid}`} name="organization" placeholder="Organization Name" autoFocus aria-required="true" />
					                  {organizationInvalid && error.errors.organization.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					                  </div>
					                </div>
					              <div className="form-group">
					                <div className="form-group field-signupform-firstname required">
					                  <input value={this.state.firstname} onChange={this.inputChanged.bind(this,'firstname')} type="text" id="signupform-firstname" className={`form-control ${firstnameInvalid}`} name="firstname" placeholder="First Name" aria-required="true" />
					                  {firstnameInvalid && error.errors.firstname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					                </div>
					              </div>
					              <div className="form-group">
					                <div className="form-group field-signupform-lastname required">
					                  <input value={this.state.lastname} onChange={this.inputChanged.bind(this,'lastname')} type="text" id="signupform-lastname" className={`form-control ${lastnameInvalid}`} name="lastname" placeholder="Last Name" aria-required="true" />
					                  {lastnameInvalid && error.errors.lastname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					                </div>
					              </div> 
					              <div className="form-group">
					                <div className="form-group field-signupform-phonenumber required">
					                  <input value={this.state.phonenumber} onChange={this.inputChanged.bind(this,'phonenumber')} type="text" id="signupform-phonenumber" className={`form-control ${phonenumberInvalid}`} name="phonenumber" placeholder="Phone Number" aria-required="true" />
					                  {phonenumberInvalid && error.errors.phonenumber.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					                </div>
					              </div> 
					              <div className="form-group">
					                <div className="form-group field-signupform-email required">
					                  <input value={this.state.email} onChange={this.inputChanged.bind(this,'email')} type="text" id="signupform-email" className={`form-control ${emailInvalid}`} name="email" placeholder="Email" aria-required="true" />
					                  {emailInvalid && error.errors.email.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					                </div>
					              </div>
					              <div className="form-group">
					                <div className="form-group field-signupform-password required">
					                  <input value={this.state.password} onChange={this.inputChanged.bind(this,'password')} type="password" id="signupform-password" className={`form-control ${passwordInvalid}`} name="password" placeholder="Password" aria-required="true" />
					                  {passwordInvalid && error.errors.password.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					                </div>
					              </div>
					              <div className="form-group">
					                <div className="form-group field-signupform-password_confirmation required">
					                  <input value={this.state.password_confirmation} onChange={this.inputChanged.bind(this,'password_confirmation')} type="password" id="signupform-password_confirmation" className={`form-control ${confirmPasswordInvalid}`} name="password_confirmation" placeholder="Confirm Password" aria-required="true" />
					                  {confirmPasswordInvalid && error.errors.password_confirmation.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					                </div>
					              </div>
					            </div>
					            <div className="footer text-center">
					              <button type="submit" disabled={this.state.loading} className="btn btn-fill btn-neutral btn-wd" name="signup-button">Create Free Account</button>                        </div>
					            </div>
					        </div>
					      </div>
					    </div>
					  </div>
					</form>

	}
}